*,
*::before,
*::after {
	box-sizing: inherit;
	font-family: MUSEOSANSCYRL, sans-serif;
}

html {
	box-sizing: border-box;
	scroll-behavior: smooth;
}

body {
	-webkit-font-smoothing: antialiased;
	text-decoration-skip: objects;
	text-rendering: optimizeLegibility;
	text-size-adjust: 100%;
	-webkit-tap-highlight-color: transparent;
	background: url(../images/sprites/jpg/bg.jpg) center/100% no-repeat;
}

button:not([disabled]),
a[role="button"]:not([disabled]) {
	cursor: pointer;
}
.container {
	width: 81.250vw;
	margin: 0 auto;
}
.animate {
	&-Left {
		display: inline-block;
		position: absolute;
		min-width: 25vw;
		max-width: 25vw;
		min-height: 15vw;
		max-height: 23vw;
		top: 11vw;
		bottom: 0;
		right: 0;
		left: 2vw;
		img {
			width: 100%;
			height: 100%;
		}
	}
	&-Right {
		display: inline-block;
		position: absolute;
		min-width: 2vw;
		max-width: 10vw;
		min-height: 20vw;
		max-height: 20vw;
		top: 1vw;
		bottom: 0;
		right: 0;
		left: 90vw;
		img {
			width: 100%;
			height: 100%;
		}
	}
	&-Center {
		display: inline-block;
		position: absolute;
		min-width: 8vw;
		max-width: 8vw;
		min-height: 8vw;
		max-height: 8vw;
		top: 31vw;
		bottom: 0;
		right: 0;
		left: 51vw;
		img {
			position: absolute;
			top: 100;
			width: 100%;
			height: 100%;
		}
		&__info {
			position: absolute;
			width: 25%;
			height: 25%;
			top: 10.5vw;
			bottom: 0;
			right: 0;
			left: 2.3vw;
			border-radius: 50%;
			background-color: rgb(244, 160, 164);
			box-shadow: 1px 2px 4px 2px rgb(212, 112, 119);
			p {
				text-align: center;
				width: 100%;
				font-family: Appetite;
				font-size: 2vw;
				color: $color-white;
				margin: 0 auto;
			}
			&:hover .animate-Center__info-descr{
				display: block;
			}
			&-descr {
				display: none;
				position: absolute;
				bottom: 0;
				left: 30px;
				width: 150px;
				height: 250px;
				font-size: 15px;
				background-color: #fff;
				border-top-left-radius: 25px;
				border-top-right-radius: 25px;
				padding: 20px 10px;
				overflow: scroll;
				text-align: center;
			}
		}
	}
	&-bottom {
		display: inline-block;
		position: absolute;
		min-width: 1.563vw;
		max-width: 1.563vw;
		min-height: 1.563vw;
		max-height: 1.563vw;
		top: 49.6vw;
		border-radius: 50%;
		border: 1px solid #ef8a90;
		left: 49.2%;
		&:after {
			content: "";
			display: inline-block;
			position: absolute;
			top: 2.5vw;
			left: .45vw;
			width: 0.521vw;
			height: 0.521vw;
			border: 2px solid #ef8a90;
			border-radius: 50%;
		}
		&-arrow {
			border: solid black;
			border-width: 0 0.156vw 0.156vw 0;
			display: inline-block;
			position: absolute;
			padding: 0.25vw;
			top: 20%;
			left: 25%;
			transform: rotate(45deg);
			-webkit-transform: rotate(45deg);
			&:after {
				content: "";
				display: inline-block;
				position: absolute;
				top: .8vw;
				left: 1.3vw;
				width: 0.052vw;
				height: 1.042vw;
				background-color: #ef8a90;
				transform: rotate(-45deg);
			}
		}
	}
}
			
