@media (max-width: 1024px) {
	.container {
		width: 90vw;
	}
	.popup {
		display: none;
		&__wrapper {
			align-items: center;
			min-width: 400px;
			min-height: 400px;
			max-width: 400px;
			max-height: 400px;
			&-img {
				min-width: 200px;
				max-width: 200px;
				min-height: 300px;
				max-height: 300px;
			}
		}
		&__close {
			width: 15px;
			height: 15px;
		}
		&__title {
			p {
				margin: 0;
				color: $color-font;
				font-size: 1.875vw;
				font-weight: 300;
				span {
					font-weight: 800;
					  
				}
			}
		}
		&__subtitle {
			height: 110px;
			font-size: 16px;
			z-index: 1000;
		}
		&__form {
			justify-content: space-evenly;
			min-width: 100%;
			min-height: 200px;
			z-index: 4;
		}
		&-succes{
			position: fixed;
			display: none;
			justify-content: center;
			align-items: center;
			top: 0px;
			right: 0px;
			bottom: 0px;
			left: 0px;
			background-color: rgba($color: #000000, $alpha: .5);
			z-index: 10000;
			text-align: center;
			&__wrapper{
				padding: 3vw 4vw;
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				align-items: center;
				min-width: 27.344vw;
				min-height: 17.448vw;
				max-width: 27.344vw;
				max-height: 17.448vw;
				background-color: #fdf3ef;
				border-radius: 25px;
				overflow: hidden;
			}
			&__icon {
				min-width: 7.688vw;
				max-width: 7.688vw;
				width: 7.688vw;
				height: 7.688vw;
				min-height: 7.688vw;
				max-height: 7.688vw;
				img {
					width: 100%;
					height: 100%;
				}
			}
			&__title {
				font-size: 1.563vw;
				font-weight: 800;
				color: $color-font;
			}
			&__subtitle {
				font-size: 1.563vw;
				font-weight: 300;
				color: $color-font;
			}
			&__end-img {
				position: absolute;
				width: 14.844vw;
				height: 14.844vw;
				top: 8.5vw;
				left: 22vw;
			}
		}
	}
}