@media (max-width: 768px) {
	.header {
		position: relative;
		&__content {
			flex-wrap: wrap;
		}
		&__address {
			&-descr {
				p {
					font-size: 9px;
					span {
						font-size: 8px;
					}
				}
			}
			&-map {
				a {
					font-size: 9px;
				}
			}
		}
		&__tell{
			&-number {
				&-phone {
					p {
						font-size: 8px;
					}
				}
			}
		}
		&__schedule-time {
			p {
				font-size: 9px;
			}
		}
		&__descr {
			display: none;
		}
		&__nav {
			width: 30px;
			height: 20px;
			position: fixed;
			top: 80px;
			left: 10px;
			cursor: pointer;
			transition: all .2s;
			&:before{
				position: absolute;
				content: '';
				top: 7px;
				left: 7px;
				width: 15px;
				height: 2px;
				background-color: #708090;
				transition: all .2s;
			}
			&:after {
				position: absolute;
				content: '';
				bottom: 7px;
				left: 7px;
				width: 15px;
				height: 2px;
				background-color: #708090;
				transition: all .2s;
			}
			&-active {
				&:after {
					bottom: 11px;
					transform: rotate(45deg);
				}
				&:before {
					top: 11px;
					transform: rotate(-45deg);
				}
			}			
			&-ul {
				position: fixed;
				top: 0;
				left: -100vw;
				width: 100vw;
				height: 100vh;
				flex-direction: column;
				justify-content: space-around;
				align-items: center;
				padding: 0;
				background-color: rgba($color: #f2ece4, $alpha: .9);
				transition: all .2s;
				z-index: 100;
				a {
					font-size: 17px;
					color: $color-white;
				}
			}
			&-item {
				a {
					color: $color-font;
				}
			}
		}
	}
	.button {
		&__question {
			min-width: 137px;
			min-height: 35px;
			max-width: 137px;
			max-height: 35px;
			font-size: 7px;
		}
		&__sing {
			position: fixed;
			top: 100px;
			right: -45px;
			transform: rotate(90deg);
		}
	}
	.question {
		margin-bottom: 0;
		&__button {
			&-wrapper {
				img {
					top: 5px;
					left: 90px;
				}
			}
		}
	}
	.question__propose {
		width: 43%;
	}
	.question__propose-item {
		font-size: 9px;
	}
	.animate-bottom {
		top: 43.5vw;
	}
	.animate-Center {
		top: 24.5vw;
	}
	.popup {
		&__wrapper {
			align-items: center;
			min-width: 400px;
			min-height: 400px;
			max-width: 400px;
			max-height: 400px;
			&-img {
				min-width: 200px;
				max-width: 200px;
				min-height: 300px;
				max-height: 300px;
				left: -100px;
			}
		}
		&__close {
			width: 15px;
			height: 15px;
			top: 10px;
			right: 20px;
		}
		&__title {
			p {
				margin: 0;
				color: $color-font;
				font-size: 17px;
				font-weight: 300;
				span {
					font-weight: 800;
					  
				}
			}
		}
		&__subtitle {
			height: 110px;
			z-index: 1000;
			p {
				font-size: 13px;
			}
		}
		&__form {
			justify-content: space-evenly;
			min-width: 100%;
			min-height: 200px;
			z-index: 4;
			input {
				font-size: 14px;
				height: 50px;
			}
			&-img {
				width: 120px;
				height: 40px;
				bottom: 50px;
			}
		}
		&-succes{
			position: fixed;
			justify-content: center;
			align-items: center;
			top: 0px;
			right: 0px;
			bottom: 0px;
			left: 0px;
			background-color: rgba($color: #000000, $alpha: .5);
			z-index: 10000;
			text-align: center;
			&__wrapper{
				padding: 3vw 4vw;
				position: relative;
				flex-direction: column;
				justify-content: flex-start;
				align-items: center;
				min-width: 27.344vw;
				min-height: 17.448vw;
				max-width: 27.344vw;
				max-height: 17.448vw;
				background-color: #fdf3ef;
				border-radius: 25px;
				overflow: hidden;
			}
			&__icon {
				min-width: 7.688vw;
				max-width: 7.688vw;
				width: 7.688vw;
				height: 7.688vw;
				min-height: 7.688vw;
				max-height: 7.688vw;
				img {
					width: 100%;
					height: 100%;
				}
			}
			&__title {
				font-size: 1.563vw;
				font-weight: 800;
				color: $color-font;
			}
			&__subtitle {
				font-size: 1.563vw;
				font-weight: 300;
				color: $color-font;
			}
			&__end-img {
				position: absolute;
				width: 14.844vw;
				height: 14.844vw;
				top: 8.5vw;
				left: 22vw;
			}
		}
	}
	.button__popup {
		font-size: 14px;
		height: 45px;
	}
	.animate-Center__info-descr {
		width: 100px;
		height: 150px;
		font-size: 10px;
	}
}