.header {
	&__wrapper {
		width: 100%;
		min-height: 10.938vw;
		height: 10.938vw;
		padding-top: 1.719vw;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		margin-bottom: 5.990vw;
	}
	&__content {
		display: flex;
		justify-content: space-between;
		width: 100%;
		min-height: 6.667vw;
		height: 6.667vw;
		padding: 0 0.781vw;
		display: flex;
		flex-direction: row;
		margin-bottom: 0.781vw;
	}
	&__logo {
		min-width: 14.583vw;
		max-width: 14.583vw;
		height: 6.563vw;
		margin-right: 2.083vw;
		img {
			width: 100%;
			height: 100%;
		}
	}
	&__descr {
		min-width: 7.952vw;
		display: flex;
		align-items: center;
		height: 100%;
		margin-right: 4.5vw;
		align-items: center;
		h2 {
			font-size: 0.833vw;
			font-weight: 300;
			color: $color-font;
			text-align: left;
			margin: 0;
		}
	}
	&__address {
		min-width: 18vw;
		height: 100%;
		display: flex;
		align-items: center;
		margin-right: 3.125vw;
		&-logo {
			min-width: 4.115vw;
			width: 4.115vw;
			max-width: 4.115vw;
			min-height: 4.115vw;
			height: 4.115vw;
			min-height: 4.115vw;
			img {
				width: 100%;
				height: 100%;
			}
		}
		&-descr {
			display: flex;
			justify-content: center;
			align-items: flex-start;
			flex-direction: column;
			p {
				font-size: 0.833vw;
				font-weight: 300;
				color: $color-font;
				margin: 0;
				span {
					font-size: 0.833vw;
					font-weight: 800;
				}
			}
		}
		&-map {
			position: relative;
			margin-top: 1.042vw;
			a {
				font-size: 0.625vw;
				font-weight: 300;
				color: $color-pink;
				text-transform: uppercase;
				text-decoration: underline;
				text-decoration-style: dashed;
				&:hover .map {
					display: block;
				}
			}
		}
	}
	&__number {
		display: flex;
		flex-direction: column;
		min-width: 18.310vw;
	}
	&__tell {
		&-number {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			margin-bottom: 0.521vw;
			&-logo {
				display: flex;
				justify-content: center;;
				align-items: center;
				min-width: 2.083vw;
				max-width: 2.083vw;
				min-height: 2.083vw;
				max-height: 2.083vw;
				border: 1px solid $color-white;
				border-radius: 50%;
				background-color: $color-white;
				margin-right: 0.521vw;
				img {
					width: 0.833vw;
					height: 0.833vw;
				}
			}
			&-phone {
				font-weight: 300;
				color: $color-font;
				p {
					font-size: 0.833vw;
					margin: 0;
					margin-bottom: 0.625vw;
					&:before {
						content: "";
						display: inline-block;
						min-width: 0.313vw;
						max-width: 0.313vw;
						min-height: 0.313vw;
						max-height: 0.313vw;
						background-color: rgb(39, 178, 75);
						border-radius: 50%;
						box-shadow: 1px 1px 3px 1px rgb(39, 178, 75);
						margin-right: 0.521vw;
						animation: pulseGreen 2s infinite;

					}
				}
				a {
					font-size: 1.250vw;
					font-weight: 300;
					color: $color-font;
					text-decoration: none;
				}
			}
		}
		
	}
	&__schedule{
		display: flex;
		align-items: center;
		&-logo {
			display: flex;
			justify-content: center;;
			align-items: center;
			min-width: 2.083vw;
			max-width: 2.083vw;
			min-height: 2.083vw;
			max-height: 2.083vw;
			border: 1px solid $color-white;
			border-radius: 50%;
			background-color: $color-white;
			margin-right: 0.521vw;
			img {
				width: 0.833vw;
				height: 0.833vw;
			}
		}
		&-time {
			font-weight: 300;
			color: $color-font;
			font-size: 0.833vw;
			p {
				margin: 0;
			}
			span {
				font-weight: 800;
			}
		}
	}
	&__sing {
		display: flex;
		align-items: center;
	}
	&__nav {
		display: flex;
		justify-content: flex-start;
		align-items: center;	
		width: 100%;
		min-height: 3.125vw;
		height: 3.125vw;
		background-color: $color-white;
		border-radius: 0.260vw;
		box-shadow: 0 2px 0 0 rgba($color: #000000, $alpha: .4);
		z-index: 100;
		&-ul {
			width: 100%;
			list-style: none;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 0;
			padding: 0 1.823vw;
		}
		&-item {
			font-weight: 300;
			letter-spacing: normal;
			&-active {
				font-weight: 800;
			}
			a {
				transition: all .3s;
				display: block;
				color: rgb(40, 40, 40);
				text-decoration: none;
				font-size: 0.781vw;
				&:hover {
					color: $color-pink;
				}
			}
			
		}
	}
}	