.pulse {
	border-radius: 50%;
	cursor: pointer;
	box-shadow: 0 0 0 rgba(204, 169, 44, 0.4);
	animation: pulse 2s infinite;
	&-white {
		box-shadow: 0 0 0 rgba(255, 255, 255, 0.4);
		animation: pulseWhite 2s infinite;
	}
	&-green {
		box-shadow: 0 0 0 rgba(39, 178, 75, 0.4);
		animation: pulseGreen 2s infinite;
	}
}

.pulse:hover {
	animation: none;
}

@keyframes pulse {
	0% {
		-moz-box-shadow: 0 0 0 0 rgba(244, 160, 164, 0.4);
		box-shadow: 0 0 0 0 rgba(244, 160, 164, 0.4);
	}

	70% {
		-moz-box-shadow: 0 0 0 30px rgba(255, 255, 255, 0);
		box-shadow: 0 0 0 30px rgba(244, 160, 164, 0);
	}

	100% {
		-moz-box-shadow: 0 0 0 0 rgba(244, 160, 164, 0);
		box-shadow: 0 0 0 0 rgba(244, 160, 164, 0);
	}
}
@keyframes pulseWhite {
	0% {
		-moz-box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.8);
		box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.8);
	}

	70% {
		-moz-box-shadow: 0 0 0 50px rgba(255, 255, 255, 0);
		box-shadow: 0 0 0 50px rgba(255, 255, 255, 0);
	}

	100% {
		-moz-box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
		box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
	}
}
@keyframes pulseGreen {
	0% {
		-moz-box-shadow: 0 0 0 0 rgba(39, 178, 75, 0.4);
		box-shadow: 0 0 0 0 rgba(39, 178, 75, 0.4);
	}

	70% {
		-moz-box-shadow: 0 0 0 10px rgba(39, 178, 75, 0);
		box-shadow: 0 0 0 10px rgba(39, 178, 75, 0);
	}

	100% {
		-moz-box-shadow: 0 0 0 0 rgba(39, 178, 75, 0);
		box-shadow: 0 0 0 0 rgba(39, 178, 75, 0);
	}
}