@media (max-width: 450px) {
	.header__address {
		margin-right: 0;
	}
	.header__content {
		height: 100%;
	}
	.header__wrapper {
		min-height: 80px;
		height: 80px;
		margin-bottom: 10px;
	}
	.header__logo {
		min-width: 120px;
		max-width: 120px;
		height: 40px;
	}
	.header__number {
		display: none
	}
	.header__address-map {
		a {
			font-weight: 800;
			color: $color-font;
			&:hover .map {
				display: none;
			}
		}
	}
	.question {
		min-height: 100px;
		max-height: 100px;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: stretch;
		&__title {
			margin-bottom: 0;
			h1 {
				font-size: 11px;
				line-height: normal;
				span {
					color: $color-pink;
				}
			}
		}
		&__propose {
			display: none;
		}
	}
	.question__button-wrapper {
		img {
			display: none;
		}
	}
	.button__question {
		padding: 0;
		min-width: 110px;
		max-width: 110px;
		min-height: 30px;
		max-height: 30px;
		
	}
	.animate-bottom {
		display: none;
	}
	.popup {
		&__wrapper {
			justify-content: space-between;
			align-items: center;
			min-width: 300px;
			min-height: 400px;
			max-width: 300px;
			max-height: 400px;
			padding-top: 20px;
			&-img {
				display: none;
			}
		}
		&__close {
			width: 15px;
			height: 15px;
		}
		&__title {
			p {
				margin: 0;
				color: $color-font;
				font-size: 17px;
				font-weight: 300;
				span {
					font-weight: 800;
					  
				}
			}
		}
		&__subtitle {
			height: 80px;
			font-size: 16px;
			z-index: 1000;
		}
		&__form {
			justify-content: space-evenly;
			min-width: 100%;
			min-height: 200px;
			z-index: 4;
			input {
				font-size: 13px;
				height: 50px;
			}
			&-img {
				width: 120px;
				height: 40px;
			}
		}
		&-succes{
			position: fixed;
			justify-content: center;
			align-items: center;
			top: 0px;
			right: 0px;
			bottom: 0px;
			left: 0px;
			background-color: rgba($color: #000000, $alpha: .5);
			z-index: 10000;
			text-align: center;
		}
	}
	.button__popup {
		height: 45px;
	}
}
@media (max-width: 320px) {
	.header__nav {
		left: 5px;
	}
	.header__wrapper {
		min-height: 70px;
   		height: 70px;
	}
}