@media (max-width: 576px) {
	.header__content {
		height: 100%;
	}
	.header__logo {
		margin-right: 0;
	}
	.header__address {
		margin-right: 0;
	}
	.header__address-logo{
		display: none;
	}
	.header__address-descr {
		p {
			font-size: 10px;
			span {
				font-size: 11px;
			}
		}
	}
	.header__tell-number-logo {
		display: none;
	}
	.header__tell-number-phone {
		p {
			font-size: 10px;
		}
		a {
			font-size: 12px;
		}
	}
	.header__schedule-logo {
		display: none;
	}
	.question__propose {
		width: 200px;
		border-radius: 15px;
		background-color: #f3f4f3;
	}
	.animate-Center {
		display: none;
	}
	.popup {
		&__wrapper {
			justify-content: space-between;
			align-items: center;
			min-width: 350px;
			min-height: 400px;
			max-width: 350px;
			max-height: 400px;
			&-img {
				min-width: 200px;
				max-width: 200px;
				min-height: 300px;
				max-height: 300px;
				left: -100px;
			}
		}
		&__close {
			width: 15px;
			height: 15px;
		}
		&__title {
			p {
				margin: 0;
				color: $color-font;
				font-size: 17px;
				font-weight: 300;
				span {
					font-weight: 800;
					  
				}
			}
		}
		&__subtitle {
			height: 80px;
			font-size: 16px;
			z-index: 1000;
		}
		&__form {
			justify-content: space-evenly;
			min-width: 100%;
			min-height: 200px;
			z-index: 4;
			input {
				font-size: 14px;
				height: 50px;
			}
			&-img {
				width: 120px;
				height: 40px;
				bottom: 20px;
				right: -20px;
			}
		}
		&-succes{
			position: fixed;
			justify-content: center;
			align-items: center;
			top: 0px;
			right: 0px;
			bottom: 0px;
			left: 0px;
			background-color: rgba($color: #000000, $alpha: .5);
			z-index: 10000;
			text-align: center;
			&__wrapper{
				padding: 3vw 4vw;
				position: relative;
				flex-direction: column;
				justify-content: flex-start;
				align-items: center;
				min-width: 200px;
				min-height: 150px;
				max-width: 200px;
				max-height: 150px;
				background-color: #fdf3ef;
				border-radius: 25px;
				overflow: hidden;
			}
			&__icon {
				min-width: 80px;
				max-width: 80px;
				width: 80px;
				height: 80px;
				min-height: 80px;
				max-height: 80px;
				img {
					width: 100%;
					height: 100%;
				}
			}
			&__title {
				font-size: 15px;
				font-weight: 800;
				color: $color-font;
			}
			&__subtitle {
				font-size: 13px;
				font-weight: 300;
				color: $color-font;
			}
			&__end-img {
				position: absolute;
				width: 150px;
				height: 150px;
				top: 50px;
				left: 150px;
			}
		}
	}
	.button__popup {
		height: 45px;
	}
}