.question {
	padding-left: 5.990vw;
	min-width: 35.417vw;
	min-height: 28.646vw;
	max-height: 28.646vw;
	margin-bottom: 12.448vw;
	&__title {
		h1 {
			font-size: 2.500vw;
			font-weight: 300;
			color: $color-font;
			margin: 0;
			line-height: 1.963vw;
		}
		span {
			font-weight: 800;
		}
		margin-bottom: 3.385vw;
	}
	&__propose {
		list-style: none;
		padding: 0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		margin: 0;
		margin-bottom: 3.125vw;
		&-item {
			font-size: 0.938vw;
			font-weight: 300;
			color: $color-font;
			  &:before {
				margin: auto 0;
				content: "";
				min-width: 0.781vw;
				min-height: 0.781vw;
				border-radius: 50%;
				border: 3px solid $color-white;
				display: inline-block	;
				background-color: #ef9499;
				margin-right: 0.781vw;
				box-shadow: .5px .5px 5px .5px #ef9499;
			  }
		}
	}
	&__button {
		&-wrapper {
			display: inline-block;
			position: relative;
			img {
				position: absolute;
				top: 0.156vw;
				left: 10.813vw;
				min-width: 24.688vw;
				width: 24.688vw;
				max-width: 24.688vw;
				min-height: 4.124vw;
				height: 4.124vw;
				max-height: 4.124vw;
			}
		}
	}
}