.button {
	//new button
	&__sing {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: 11.719vw;
		min-height: 4.014vw;
		border: none;
		border-bottom: 0.104vw solid rgb(235, 234, 234);
		border-radius: 30vw;
		background-color: rgb(254, 252, 251);
		outline: none;
		&:active {
			background-color: rgb(235, 234, 234);
			border-bottom: 0.104vw solid rgb(254, 252, 251);
		}
		p {
			font-size: 0.781vw;
			font-weight: 300;
			color: $color-font;
			margin: 0;
		}
	}
	&__calendar{
		&-img {
			min-width: 1.042vw;
			min-height: 1.042vw;
			margin-right: 0.521vw;
		}
	}
	// new button
	&__question {
		display: inline-block;
		min-width: 19.531vw;
		min-height: 4.688vw;
		max-width: 19.531vw;
		max-height: 4.688vw;
		padding: 0 7.917vw 0 1.563vw;
		border-radius: 2.240vw;
		border: none;
		outline: none;
		background-image: -moz-linear-gradient( 90deg, rgb(238,133,140) 0%, rgb(244,161,164) 55%, rgb(250,188,188) 98%);
		background-image: -webkit-linear-gradient( 90deg, rgb(238,133,140) 0%, rgb(244,161,164) 55%, rgb(250,188,188) 98%);
		background-image: -ms-linear-gradient( 90deg, rgb(238,133,140) 0%, rgb(244,161,164) 55%, rgb(250,188,188) 98%);
		font-size: 0.729vw;
		color: $color-white;
		text-transform: uppercase;
		text-align: center;
		text-shadow: 0 0.052vw 0.260vw rgba(210, 110, 116, 0.49);
		margin: 0;
		&:active {
			background-image: -moz-linear-gradient( 90deg, rgba(238, 133, 140, 0.877) 0%, rgba(244, 161, 164, 0.781) 55%, rgba(250, 188, 188, 0.76) 98%);
			background-image: -webkit-linear-gradient( 90deg, rgba(238, 133, 140, 0.877) 0%, rgba(244, 161, 164, 0.781) 55%, rgba(250, 188, 188, 0.76) 98%);
			background-image: -ms-linear-gradient( 90deg, rgba(238, 133, 140, 0.877) 0%, rgba(244, 161, 164, 0.781) 55%, rgba(250, 188, 188, 0.76) 98%);
		}
	}
	//new button
	&__popup {
		width: 100%;
		height: 4.167vw;
		border-radius: 25px;
		border: none;
		background-color: #f39ea2;
		color: $color-white;
		font-size: 0.938vw;
		font-weight: 500;
		text-transform: uppercase;
		text-align: center;
		text-shadow: 0px 1px 5px rgba(210, 110, 116, 0.49);
		outline: none;
		&:active {
			background-image: -moz-linear-gradient( 90deg, rgba(238, 133, 140, 0.877) 0%, rgba(244, 161, 164, 0.781) 55%, rgba(250, 188, 188, 0.76) 98%);
			background-image: -webkit-linear-gradient( 90deg, rgba(238, 133, 140, 0.877) 0%, rgba(244, 161, 164, 0.781) 55%, rgba(250, 188, 188, 0.76) 98%);
			background-image: -ms-linear-gradient( 90deg, rgba(238, 133, 140, 0.877) 0%, rgba(244, 161, 164, 0.781) 55%, rgba(250, 188, 188, 0.76) 98%);
		}
	}
}