.popup {
	position: fixed;
	display: none;
	justify-content: center;
	align-items: center;
	top: 0px;
	right: 0px;
	bottom: 0px;
	left: 0px;
	background-color: rgba($color: #000000, $alpha: .5);
	z-index: 10000;
	text-align: center;
	&__wrapper {
		padding: 3vw 4vw;
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		min-width: 34.896vw;
		min-height: 34.896vw;
		max-width: 34.896vw;
		max-height: 34.896vw;
		background-color: #fdf3ef;
		border-radius: 25px;
		&-img {
			position: absolute;
			min-width: 14vw;
			max-width: 14vw;
			min-height: 20vw;
			max-height: 20vw;
			left: -7vw;
			bottom: 2.8vw;
		}
	}
	&__close {
		position: absolute;
		top: 1vw;
		right: 1.5vw;
		cursor: pointer;
		width: 0.677vw;
		height: 0.677vw;
	}
	&__title {
		p {
			margin: 0;
			color: $color-font;
			font-size: 1.875vw;
			font-weight: 300;
			span {
				font-weight: 800;
				  
			}
		}
	}
	&__subtitle {
		overflow: scroll;
		height: 2.604vw;
		font-size: 16px;
		font-weight: 300;
		color: $color-font;
		margin: 0.521vw 0;
		p {
			margin: 0;
		}
	}
	&__form {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		min-width: 26.042vw;
		min-height: 19.271vw;
		z-index: 4;
		input {
			padding: 1.563vw 2.083vw;
			width: 100%;
			height: 4.167vw;
			border: 1px solid #fbeff0;
			border-radius: 25px;
			outline: none;
			font-size: 0.938vw;
			font-weight: 500;
			color: rgb(152, 151, 151);
			  
		}
		&-img{
			position: absolute;
			z-index: 5;
			bottom: 4.7vw;
			right: .1vw;
			width: 12.031vw;
			height: 3.408vw;
		}
	}
	&-succes{
		position: fixed;
		display: none;
		justify-content: center;
		align-items: center;
		top: 0px;
		right: 0px;
		bottom: 0px;
		left: 0px;
		background-color: rgba($color: #000000, $alpha: .5);
		z-index: 10000;
		text-align: center;
		&__wrapper{
			padding: 3vw 4vw;
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			min-width: 27.344vw;
			min-height: 17.448vw;
			max-width: 27.344vw;
			max-height: 17.448vw;
			background-color: #fdf3ef;
			border-radius: 25px;
			overflow: hidden;
		}
		&__icon {
			min-width: 7.688vw;
			max-width: 7.688vw;
			width: 7.688vw;
			height: 7.688vw;
			min-height: 7.688vw;
			max-height: 7.688vw;
			img {
				width: 100%;
				height: 100%;
			}
		}
		&__title {
			font-size: 1.563vw;
			font-weight: 800;
			color: $color-font;
		}
		&__subtitle {
			font-size: 1.563vw;
			font-weight: 300;
			color: $color-font;
		}
		&__end-img {
			position: absolute;
			width: 14.844vw;
			height: 14.844vw;
			top: 8.5vw;
			left: 22vw;
		}
	}
}